import { GameState, InputState } from './types';
import { InputManager } from './InputManager';
import { Renderer } from './Renderer';
import { Collision } from './Collision';

export class Game {
  private canvas: HTMLCanvasElement;
  private ctx: CanvasRenderingContext2D;
  private inputManager: InputManager;
  private renderer: Renderer;
  private gameState: GameState;
  private lastTime: number = 0;
  private animationId: number | null = null;
  private isRunning: boolean = false;

  constructor(canvas: HTMLCanvasElement) {
    this.canvas = canvas;
    this.ctx = canvas.getContext('2d')!;
    this.inputManager = new InputManager();
    this.renderer = new Renderer(canvas);
    this.gameState = this.initializeGameState();
  }

  private initializeGameState(): GameState {
    return {
      players: [
        {
          position: { x: 200, y: 550 }, // Bottom left area
          velocity: { x: 0, y: 0 },
          width: 40,
          height: 40,
          rotation: 0,
          health: 3,
          maxHealth: 3,
          score: 0,
          lastShot: 0,
          powerUp: null,
          powerUpEndTime: 0
        },
        {
          position: { x: 600, y: 550 }, // Bottom right area
          velocity: { x: 0, y: 0 },
          width: 40,
          height: 40,
          rotation: 0,
          health: 3,
          maxHealth: 3,
          score: 0,
          lastShot: 0,
          powerUp: null,
          powerUpEndTime: 0
        }
      ],
      bullets: [],
      enemies: [],
      particles: [],
      powerUps: [],
      gameStatus: 'menu',
      wave: 1,
      lastEnemySpawn: 0
    };
  }

  public start() {
    if (this.isRunning) return;
    
    this.isRunning = true;
    this.gameState.gameStatus = 'playing';
    this.lastTime = performance.now();
    this.gameLoop();
  }

  public pause() {
    this.gameState.gameStatus = 'paused';
  }

  public resume() {
    this.gameState.gameStatus = 'playing';
  }

  public restart() {
    this.gameState = this.initializeGameState();
    this.start();
  }

  public stop() {
    this.isRunning = false;
    if (this.animationId) {
      cancelAnimationFrame(this.animationId);
      this.animationId = null;
    }
  }

  private gameLoop = () => {
    if (!this.isRunning) return;

    const currentTime = performance.now();
    const deltaTime = (currentTime - this.lastTime) / 1000;
    this.lastTime = currentTime;

    if (this.gameState.gameStatus === 'playing') {
      this.update(deltaTime);
    }

    this.render();
    this.animationId = requestAnimationFrame(this.gameLoop);
  };

  private update(deltaTime: number) {
    const inputState = this.inputManager.updateInputState();
    
    this.updatePlayers(inputState, deltaTime);
    this.updateBullets(deltaTime);
    this.updateEnemies(deltaTime);
    this.updateParticles(deltaTime);
    this.updatePowerUps(deltaTime);
    this.spawnEnemies(deltaTime);
    this.checkCollisions();
    this.cleanupObjects();
  }

  private updatePlayers(inputState: InputState, deltaTime: number) {
    const speed = 300 * deltaTime;
    
    // Player 1 - Only horizontal movement
    const player1 = this.gameState.players[0];
    player1.velocity = { x: 0, y: 0 };
    
    // Only allow left and right movement
    if (inputState.player1.left) player1.velocity.x = -speed;
    if (inputState.player1.right) player1.velocity.x = speed;
    
    player1.position.x += player1.velocity.x;
    
    Collision.clampToBounds(player1, this.canvas.width, this.canvas.height);
    
    // Player 2 - Only horizontal movement
    const player2 = this.gameState.players[1];
    player2.velocity = { x: 0, y: 0 };
    
    // Only allow left and right movement
    if (inputState.player2.left) player2.velocity.x = -speed;
    if (inputState.player2.right) player2.velocity.x = speed;
    
    player2.position.x += player2.velocity.x;
    
    Collision.clampToBounds(player2, this.canvas.width, this.canvas.height);
    
    // Shooting
    this.handleShooting(inputState);
  }

  private handleShooting(inputState: InputState) {
    const now = performance.now();
    const shootCooldown = 200; // 200ms between shots
    
    // Player 1 shooting - bullets shoot upward
    if (inputState.player1.shoot && now - this.gameState.players[0].lastShot > shootCooldown) {
      this.gameState.bullets.push({
        position: { x: this.gameState.players[0].position.x, y: this.gameState.players[0].position.y - 20 },
        velocity: { x: 0, y: -500 },
        width: 8,
        height: 16,
        rotation: 0,
        damage: 1,
        owner: 'player1'
      });
      this.gameState.players[0].lastShot = now;
    }
    
    // Player 2 shooting - bullets shoot upward
    if (inputState.player2.shoot && now - this.gameState.players[1].lastShot > shootCooldown) {
      this.gameState.bullets.push({
        position: { x: this.gameState.players[1].position.x, y: this.gameState.players[1].position.y - 20 },
        velocity: { x: 0, y: -500 },
        width: 8,
        height: 16,
        rotation: 0,
        damage: 1,
        owner: 'player2'
      });
      this.gameState.players[1].lastShot = now;
    }
  }

  private updateBullets(deltaTime: number) {
    this.gameState.bullets.forEach(bullet => {
      bullet.position.x += bullet.velocity.x * deltaTime;
      bullet.position.y += bullet.velocity.y * deltaTime;
    });
  }

  private updateEnemies(deltaTime: number) {
    this.gameState.enemies.forEach(enemy => {
      enemy.position.x += enemy.velocity.x * deltaTime;
      enemy.position.y += enemy.velocity.y * deltaTime;
      
      // Simple enemy AI - move towards players
      const targetPlayer = this.gameState.players[Math.floor(Math.random() * 2)];
      const dx = targetPlayer.position.x - enemy.position.x;
      const dy = targetPlayer.position.y - enemy.position.y;
      const distance = Math.sqrt(dx * dx + dy * dy);
      
      if (distance > 0) {
        enemy.velocity.x = (dx / distance) * 100;
        enemy.velocity.y = (dy / distance) * 50;
      }
    });
  }

  private updateParticles(deltaTime: number) {
    this.gameState.particles.forEach(particle => {
      particle.position.x += particle.velocity.x * deltaTime;
      particle.position.y += particle.velocity.y * deltaTime;
      particle.life -= deltaTime;
    });
  }

  private updatePowerUps(deltaTime: number) {
    this.gameState.powerUps.forEach(powerUp => {
      powerUp.position.y += 50 * deltaTime;
    });
  }

  private spawnEnemies(deltaTime: number) {
    const spawnInterval = Math.max(1000 - (this.gameState.wave * 100), 200);
    
    if (Date.now() - this.gameState.lastEnemySpawn > spawnInterval) {
      const enemyTypes = ['basic', 'fast', 'heavy'] as const;
      const type = enemyTypes[Math.floor(Math.random() * enemyTypes.length)];
      
      this.gameState.enemies.push({
        position: { x: Math.random() * this.canvas.width, y: -30 },
        velocity: { x: 0, y: 50 },
        width: type === 'heavy' ? 50 : type === 'fast' ? 30 : 40,
        height: type === 'heavy' ? 50 : type === 'fast' ? 30 : 40,
        rotation: 0,
        health: type === 'heavy' ? 3 : type === 'fast' ? 1 : 2,
        maxHealth: type === 'heavy' ? 3 : type === 'fast' ? 1 : 2,
        type,
        lastShot: 0
      });
      
      this.gameState.lastEnemySpawn = Date.now();
    }
  }

  private checkCollisions() {
    // Bullet vs Enemy collisions
    this.gameState.bullets.forEach((bullet, bulletIndex) => {
      this.gameState.enemies.forEach((enemy, enemyIndex) => {
        if (Collision.checkCollision(bullet, enemy)) {
          enemy.health -= bullet.damage;
          this.gameState.bullets.splice(bulletIndex, 1);
          
          if (enemy.health <= 0) {
            this.gameState.enemies.splice(enemyIndex, 1);
            const playerIndex = bullet.owner === 'player1' ? 0 : 1;
            this.gameState.players[playerIndex].score += 100;
            
            // Create explosion particles
            this.createExplosion(enemy.position.x, enemy.position.y);
          }
        }
      });
    });

    // Enemy vs Player collisions
    this.gameState.enemies.forEach((enemy, enemyIndex) => {
      this.gameState.players.forEach((player, playerIndex) => {
        if (Collision.checkCollision(enemy, player)) {
          player.health -= 1;
          this.gameState.enemies.splice(enemyIndex, 1);
          
          if (player.health <= 0) {
            this.gameState.gameStatus = 'gameOver';
          }
        }
      });
    });
  }

  private createExplosion(x: number, y: number) {
    for (let i = 0; i < 8; i++) {
      const angle = (i / 8) * Math.PI * 2;
      this.gameState.particles.push({
        position: { x, y },
        velocity: {
          x: Math.cos(angle) * 200,
          y: Math.sin(angle) * 200
        },
        width: 4,
        height: 4,
        rotation: 0,
        life: 0.5,
        maxLife: 0.5,
        color: '#ff6600',
        size: 4
      });
    }
  }

  private cleanupObjects() {
    // Remove off-screen bullets
    this.gameState.bullets = this.gameState.bullets.filter(bullet => 
      !Collision.isOutOfBounds(bullet, this.canvas.width, this.canvas.height)
    );

    // Remove off-screen enemies
    this.gameState.enemies = this.gameState.enemies.filter(enemy => 
      !Collision.isOutOfBounds(enemy, this.canvas.width, this.canvas.height)
    );

    // Remove dead particles
    this.gameState.particles = this.gameState.particles.filter(particle => 
      particle.life > 0
    );

    // Remove off-screen power-ups
    this.gameState.powerUps = this.gameState.powerUps.filter(powerUp => 
      powerUp.position.y < this.canvas.height + 50
    );
  }

  private render() {
    this.renderer.clear();
    
    switch (this.gameState.gameStatus) {
      case 'menu':
        this.renderer.drawMenu();
        break;
      case 'playing':
        this.renderer.drawHUD(this.gameState);
        
        // Draw game objects
        this.gameState.players.forEach((player, index) => 
          this.renderer.drawPlayer(player, (index + 1) as 1 | 2)
        );
        
        this.gameState.bullets.forEach(bullet => 
          this.renderer.drawBullet(bullet)
        );
        
        this.gameState.enemies.forEach(enemy => 
          this.renderer.drawEnemy(enemy)
        );
        
        this.gameState.particles.forEach(particle => 
          this.renderer.drawParticle(particle)
        );
        
        this.gameState.powerUps.forEach(powerUp => 
          this.renderer.drawPowerUp(powerUp)
        );
        break;
      case 'paused':
        this.renderer.drawPause();
        break;
      case 'gameOver':
        const winner = this.gameState.players[0].score > this.gameState.players[1].score ? 
          'Player 1 Wins!' : 
          this.gameState.players[1].score > this.gameState.players[0].score ? 
          'Player 2 Wins!' : 'Tie Game!';
        this.renderer.drawGameOver(winner);
        break;
    }
  }

  public destroy() {
    this.stop();
    this.inputManager.destroy();
  }
}
