import { GameObject } from './types';

export class Collision {
  public static checkCollision(obj1: GameObject, obj2: GameObject): boolean {
    const dx = obj1.position.x - obj2.position.x;
    const dy = obj1.position.y - obj2.position.y;
    const distance = Math.sqrt(dx * dx + dy * dy);
    
    const minDistance = (obj1.width + obj2.width) / 2;
    return distance < minDistance;
  }

  public static checkRectCollision(obj1: GameObject, obj2: GameObject): boolean {
    return (
      obj1.position.x - obj1.width / 2 < obj2.position.x + obj2.width / 2 &&
      obj1.position.x + obj1.width / 2 > obj2.position.x - obj2.width / 2 &&
      obj1.position.y - obj1.height / 2 < obj2.position.y + obj2.height / 2 &&
      obj1.position.y + obj1.height / 2 > obj2.position.y - obj2.height / 2
    );
  }

  public static isOutOfBounds(obj: GameObject, width: number, height: number): boolean {
    return (
      obj.position.x < -obj.width ||
      obj.position.x > width + obj.width ||
      obj.position.y < -obj.height ||
      obj.position.y > height + obj.height
    );
  }

  public static clampToBounds(obj: GameObject, width: number, height: number): void {
    obj.position.x = Math.max(obj.width / 2, Math.min(width - obj.width / 2, obj.position.x));
    obj.position.y = Math.max(obj.height / 2, Math.min(height - obj.height / 2, obj.position.y));
  }
}
